@-ms-viewport {
    width: device-width;
}

body {
    background: #f5f7fa;
    font-family: 'Source Sans Pro', sans-serif;
    font-weight: 300;
    color: #52575c;
    line-height: 1.75em;
    font-size: 14pt;
}


.mainContainer {
    width: 100vw;
    height: 100vh;
    background-color: #2f2f2f;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}
.main_logo_area{
    height : 130px;
    height: 130px;
    margin-top: 50px;
}
.main_logo {
    position: relative;
    margin: 0 auto;
    height: 100%;
}

.user_search_form {
    position: relative;
    width: 624px;
    height: 50px;
    margin: 0 auto;
    border-radius: 2px;
    background-color: #fff;
    margin-top: 20px;
}

.user_search_form_text{
    display: block;
    width: 100%;
    padding: 15px 150px 18px 17px;
    background: none;
    border: none;
    line-height: 17px;
    font-size: 14px;
    color: #9b9b9b;
    box-sizing: border-box;
    outline: none;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.19);
}
.user_search_form_button{
    display: flex;
    position: absolute;
    top: 0;
    right: 0;
    margin: 10px 10px 0 0;
    height: 30px;
    padding: 0;
    border: none;
}

.search_button_img{
    width: 50px;
    height: 30px;
    background-image: url(../img/search_btn.png);
    background-position: -90px -797px;
}

.footerContainer{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    padding: 30px 0;
    text-align: center;
}