.champDetailContainer{
    padding-top: 50px;
    width: 100%;
    text-align: center;
}

.champDetailHeader{
    position: relative;
    overflow: hidden;
    color: #fff;
    background: #292e38;
}

.champDetailInfo{
    float: left;
    margin-top: 30px;
    margin-bottom: 24px;
    width: 610px;
}

.champDetailImgArea{
    position: relative;
    float: left;
    margin-right: 20px;
}

.champDetailImgArea img{
    display: block;
    /* width: 140px; */
    width: 90px;
    border: 4px solid #fff;
    box-sizing: border-box;
}

.champDetailName{
    margin-top: 35px;
    line-height: 29px;
    font-size: 24px;
    letter-spacing: -0.9px;
    text-align: left;
    color: #fff;
    font-weight: normal;
}

.champDetailTier{
    margin-top: 7px;
    line-height: 18px;
    font-size: 14px;
    color: #9c9c9c;
}

.champDetailSkill{
    margin-top: 10px;
}
.champDetailSkill ._skill{
    position: relative;
    float: left;
    border: 1px solid #000;
    border-radius: 2px;
    margin-left: 8px;
    width: 40px;
    height: 40px;
    overflow: hidden;
}

._skill img{
    display: block;
    width: 100%;
}

._skill ._key{
    position: absolute;
    right: 0;
    bottom: 0;
    line-height: 14px;
    font-size: 12px;
    letter-spacing: -0.5px;
    padding: 3px 4px 3px 5px;
    background: #000;
    background: rgba(0, 0, 0, 0.5);
    border-radius: 1px;
}