
.recodsContainer{
    padding-top : 50px;
    width: 100%;
    text-align: center;
}
.gameResultContainer{
    display: inline-block;
}

.userInfoContainer{
    background-color: #f1f1f1;
    padding: 30px;
    text-align: center;
    font-size: 35px;
}

.userInfoContainer .userInfoArea{
    display: inline-block;
    width: 100%;
}

.userInfoContainer .userInfoArea .row:after{
    width: 100%;
    content: "";
    display: table;
    clear: both;
}

.userLevel{
    padding-left: 5px;
}

.userColumn {
    float: left;
    width: 45%;
    margin-left: 45px;
    padding: 10px;
    height: 180px; /* Should be removed. Only for demonstration */
  }

.userColumn .userName{
    float: left;
    font-weight: 600 !important;
    font-size: 1.3rem !important;
    margin-top: 0.25rem;
    margin-bottom: 0.25rem; 
    margin-left: 1.25rem;
    
}
._userID{
    margin-left: -135px;
}
.userTotalRecods{
    display: -webkit-box;
}
.recods{
    position: relative;
    border-radius: 3px;
    margin-bottom: 8px;
}

.recods .recods_data_win{
    background-color: #9190FF;
    display: table;
    width: 689px;
    border-collapse: collapse;
    border: 1px solid #cdd2d2;
    height: 98px;
}

.recods .recods_data_lose{
    background-color: #FF6C6C;
    display: table;
    width: 689px;
    border-collapse: collapse;
    border: 1px solid #cdd2d2;
    height: 98px;
}

.gameResult{
    position: relative;
    width: 100px;
    height: 100px;
    display: table-cell;
}
.playLane{
    text-align: center;
}
.gameResultInfo{
    display: table-cell;
}

.blueTeam{
    position: relative;
    margin-bottom: 5px;
    width: 90px;
    height: 90px;
    border-radius: 50%;
    background: #0000ED;
    text-align: center;
    line-height: 150px;
}

.redTeam{
    position: relative;
    margin-bottom: 5px;
    width: 90px;
    height: 90px;
    border-radius: 50%;
    background: #FF0000;
    text-align: center;
    line-height: 150px;
}

.userColumn .userTierImg{
    width: 130px;
    height: 130px;
    background-color: #FF6C6C;
    float: left;
    margin: -5px 0 -10px 0;
}
.userTier, .userRecods{
    width: 155px;
    height: 30px;
}
.userRecods{
    font-size: 1.0rem;
}
.userWinRate, .freeWinRate{
    color: blue;
    font-size: 1.1rem;
}
.userLoseRate, .freeLoseRate{
    color: red;
    font-size: 1.1rem;
}

.soloRankRecods{
    background-color: #d6e2c0;
}

.freeRankRecods{
    background-color: #d6e2c0;
    margin-left: 2px;
}

.unranked{
    width: 155px;
    height: 83px;
    background-image: url(../img/unranked.png);
    background-repeat: no-repeat;
    background-size: 84px;
    background-position: center;
}
